.SalaryTable {
	margin: 30px 0px 0px 0px;
	padding: 5px 10px;
	border: 1px solid black;
	border-radius: 5px;
	flex: 1 1 70%;
}

.SalaryTable ul {
	list-style: none;
	padding: 0px;
	margin: 0px;
}
.SalaryTable ul li {
	padding: 10px 15px;
	margin: 10px 0px;
	border: 1px solid rgb(172, 172, 172);
	display: flex;
	justify-content: space-between;
	border-radius: 5px;
	font-family: 'Roboto';
	font-size: 20px;
	font-weight: 300;
	
}
.SalaryTable ul li span {
	flex: 1 1 33.333%;
}
.SalaryTable ul li span:last-child {
	text-align: end;
}
.close {
	cursor: pointer;
}