.InputBlock {
	border-top: 1px solid #000;
	border-bottom: 1px solid #000;
	padding: 30px 0px;
}
.InputBlock form{
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
}
.InputBlock form label{
	font: 20px 'Roboto';
	font-weight: 300;
	text-transform: uppercase;
	margin: 0px 0px 15px 0px;
}

.InputBlock form input {
	margin: 0px 0px 15px 0px;
	padding: 10px 15px;
	font-size: 30px;
	font-weight: 300;
	font-family: 'Roboto';
	border: 1px solid black;
	border-radius: 5px;
	outline: none;
	max-width: 300px;
	box-sizing: border-box;
}
.InputBlock form input::placeholder {
	color: rgb(216, 216, 216);
}
.InputBlock form button {
	padding: 10px 20px;
	font-size: 24px;
	font-weight: 300;
	font-family: 'Roboto';
	width: 300px;
	border: none;
	outline: none;
	background: black;
	color: white;
	border-radius: 5px;
	text-transform: uppercase;
	letter-spacing: 1px;
	transition: all 0.3s ease 0s;
	cursor: pointer;
}
.InputBlock form button:hover {
	transform: scale(1.05);
}
.InputBlock form button:active {
	transform: scale(1);
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}