.SalarySum {
	flex: 1 1 30%;
	margin: 30px 0px 0px 0px;
	@media (max-width: 1350px) {
		margin: 0px 0px 0px 0px;
	}
	padding: 5px 10px;
	border: 1px solid black;
	border-radius: 5px;
}

.firstblock, .secondblock {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	font-family: 'Roboto';
	margin: 20px 0px;
}

.firstblock div:first-child,
.secondblock div:first-child{
	margin: 0px 0px 20px 0px;
	font-size: 24px;
	font-weight: 300;
}
.firstblock div:last-child,
.secondblock div:last-child {
	font-size: 60px;
	font-weight: 300;
}